.container {
    position: absolute;
    left: 50%;
    top: 50%;
    padding: 10px;
    background: #FFFFFF;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12),
    0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
    border-radius: 4px;
    transform: translate(-50%, -50%);
    z-index: 777;
    @media screen and (min-width: 768px) {
        padding: 20px;
    }
    @media screen and (min-width: 1200px) {
        padding: 30px;
    }
 
}

.closeButton {
    display: flex;
    align-items: center;
    justify-content: center;
     background: transparent;
     border-color: transparent;
     width: 40px;
     height: 40px;
     border-radius: 50%;
     margin-bottom: 30px;
     color: #829AB1;
     transition: color 250ms background 250ms  cubic-bezier(0.4, 0, 0.2, 1); 
     &:hover, &:focus {
      color: #F0F4F8;
      background: red;    
      }
 }
 .closeButton svg { 
  fill: currentColor;  
   }

.label {
    margin-left: 10px;
    margin-bottom: 10px;
    color: var(--Blue-Grey-Blue-Grey-700, #334E68);
    font-family: Inter;
    font-size: 30px;
    font-style: normal;
    font-weight: 600;
}
.input {
    width: 250px;
    height: 30px;
    padding-left: 30px;
    margin-bottom: 20px;
    color: var(--Blue-Grey-Blue-Grey-400, #829AB1);
    text-align: center;
    font-family: Inter;
    font-size: 20px;
    font-weight: 400;
    border-radius: 15px;
background: var(--Blue-Grey-Blue-Grey-050, #F0F4F8);
@media screen and (min-width: 768px) {
    width: 600px;
    height: 35px;
} 
@media screen and (min-width: 1200px) {
    width: 700px;
    height: 40px;
}    
}


